// Core
import React from "react";

// UI
import { Box, Typography } from "@material-ui/core";

// Utils
import { getStartOfWeek, getEndOfWeek } from "utils/developerReportUtils";

// constants
import { DATE_AND_MONTH } from "constants/dateFormats";

export default function TotalTimeInfo({
    totalDayTime,
    totalWeekTime,
    selectedWeek,
}) {
    const getDateIntervalLabel = selectedWeek =>
        `From ${getStartOfWeek(selectedWeek).format(
            DATE_AND_MONTH
        )} to ${getEndOfWeek(selectedWeek).format(DATE_AND_MONTH)}`;

    return (
        <>
            {selectedWeek && (
                <Box display="flex">
                    <Box pl={1}>
                        <Typography
                            align="right"
                            variant="subtitle1"
                            gutterBottom
                            color="textPrimary"
                        >
                            <b>{"From "}</b>
                            {getStartOfWeek(selectedWeek).format(
                                DATE_AND_MONTH
                            )}
                            <b>{" to "}</b>
                            {getEndOfWeek(selectedWeek).format(DATE_AND_MONTH)}
                        </Typography>
                    </Box>
                </Box>
            )}
            <Box display="flex">
                <Typography variant="overline" color="textSecondary">
                    {"Total week work time:"}
                </Typography>
                <Box pl={1}>
                    <Typography
                        align="right"
                        variant="subtitle1"
                        gutterBottom
                        color="textPrimary"
                    >
                        {totalWeekTime}
                    </Typography>
                </Box>
            </Box>
            <Box display="flex">
                <Typography variant="overline" color="textSecondary">
                    {"Total day work time:"}
                </Typography>
                <Box pl={1}>
                    <Typography
                        align="right"
                        variant="subtitle1"
                        gutterBottom
                        color="textPrimary"
                    >
                        {totalDayTime}
                    </Typography>
                </Box>
            </Box>
        </>
    );
}
