import moment from "moment";

import { IMAGES_URL } from "services/api/constants";

import { DEFAULT_FORM_VALUES } from "constants/developers";
import { HOUR_IN_MINUTES } from "constants/dateFormats";

export const getInitialFormValues = initialData => {
    if (!initialData) {
        return {
            ...DEFAULT_FORM_VALUES,
        };
    }
    return Object.keys(DEFAULT_FORM_VALUES).reduce((acc, key) => {
        acc[key] = initialData[key];
        return acc;
    }, {});
};

export const formatFromMinutes = durationInMinutes => {
    if (Number(durationInMinutes) === 0) {
        return durationInMinutes;
    }
    if (durationInMinutes < HOUR_IN_MINUTES) {
        return `${durationInMinutes} minutes`;
    }
    return `${moment
        .duration(durationInMinutes, "seconds")
        .hours()}h ${moment.duration(durationInMinutes, "seconds").minutes()}m`;
};

export const getUserAvatar = avatar => (avatar ? IMAGES_URL + avatar : null);
