import moment from "moment";
import {
    FULL_DATE_FORMAT,
    TIME_FORMAT,
    MONDAY,
    SUNDAY,
    HOUR_IN_MINUTES,
} from "constants/dateFormats";

export const getInitialFormValues = initialData => ({
    date: initialData
        ? moment(initialData.date, FULL_DATE_FORMAT).format(FULL_DATE_FORMAT)
        : "",
    descriptions: initialData ? initialData.descriptions : "",
    from_time: initialData
        ? moment(initialData["from_time"], TIME_FORMAT).format(TIME_FORMAT)
        : moment("00:00", TIME_FORMAT).format(TIME_FORMAT),
    to_time: initialData
        ? moment(initialData["to_time"], TIME_FORMAT).format(TIME_FORMAT)
        : moment("00:00", TIME_FORMAT).format(TIME_FORMAT),
    project_id: initialData ? initialData["project_id"] : 0,
    log_type_id: initialData ? initialData["log_type_id"] : 0,
});

export const getTotalTime = durations => {
    if (!durations.length) return "0";
    const totalDurations = durations
        .slice(1)
        .reduce(
            (prev, cur) => moment.duration(cur.total).add(prev),
            moment.duration(durations[0].total)
        );

    const hours = Math.floor(totalDurations.clone().asHours());
    const minutes = Math.floor(totalDurations.clone().minutes());
    if (!hours) {
        return `${minutes} minutes`;
    }
    return `${hours}h ${minutes}m`;
};

export const getParametersForLogs = selectedWeek => ({
    from_date: moment()
        .isoWeek(selectedWeek)
        .isoWeekday(MONDAY)
        .format(FULL_DATE_FORMAT),
    to_date: moment()
        .isoWeek(selectedWeek)
        .isoWeekday(SUNDAY)
        .format(FULL_DATE_FORMAT),
});

export const getStartOfWeek = weekNumber =>
    moment().isoWeek(weekNumber).startOf("isoWeek");

export const getEndOfWeek = weekNumber =>
    moment().isoWeek(weekNumber).endOf("isoWeek");

export const getMonthNameByWeek = weekNumber =>
    moment().set("month", moment().isoWeek(weekNumber).month()).format("MMMM");

export const formatTimeDuration = time => {
    const durationInMinutes = moment.duration(time).asMinutes();
    if (durationInMinutes < HOUR_IN_MINUTES) {
        return `${durationInMinutes} min`;
    }
    return `${moment.duration(time).hours()}h ${moment
        .duration(time)
        .minutes()}m`;
};
