import React from "react";
import moment from "moment";
import { useFormik } from "formik";
import { object, string } from "yup";

// UI
import { Box, Typography, Paper } from "@material-ui/core";
import UserAvatar from "../../../components/UserAvatar";
import LogStatusHandler from "../LogStatusHandler";

// Constants
import { FULL_DATE_FORMAT, DATE_FORMAT } from "constants/dateFormats";
import { validationErrorMessages } from "../../../constants/developerReport";

// Utils
import { formatTimeDuration } from "utils/managerReportsUtils";

// Styles
import { StyledTextArea, StyledTypography } from "./style";

let schema = object().shape({
    descriptions: string().required(),
});

function InReviewItems({
    onUpdateLog,
    onUpdateLogStatus,
    logTypes,
    user_id,
    project_id,
    log_type_id,
    date,
    total,
    descriptions,
    status,
    id,
    from_time,
    to_time,
}) {
    const getLogType = id => {
        return logTypes.find(item => item.id === id)?.type;
    };

    function validate(values) {
        const errors = {};
        if (!values.descriptions) {
            errors.descriptions = validationErrorMessages.descriptions;
        }
        return errors;
    }

    const formik = useFormik({
        initialValues: {
            date,
            descriptions,
            from_time,
            to_time,
            project_id: project_id?.id,
            log_type_id,
        },
        validate,
        validationSchema: schema,
        validateOnChange: false,
        enableReinitialize: true,
        onSubmit: (data, { resetForm }) => {
            if (descriptions === data.descriptions) return;
            onUpdateLog(data, id);
        },
    });

    const onUpdateStatus = async (...param) => {
        const errors = await formik.validateForm();
        if (Object.keys(errors).length) return;

        await formik.submitForm();
        onUpdateLogStatus(...param);
    };

    return (
        <>
            <Box
                display="flex"
                flexWrap="wrap"
                component={Paper}
                marginBottom="10px"
                variant="outlined"
                py={1}
                px={1}
                borderRadius={8}
            >
                <Box
                    display="flex"
                    flexDirection="column"
                    justifyContent="flex-start"
                    alignItems="center"
                    flex={1}
                    px={2}
                    mb={2}
                >
                    <UserAvatar
                        size={100}
                        firstName={user_id.firstname}
                        lastName={user_id.lastname}
                        avatar={user_id.avatar}
                    />

                    <Box
                        justifyContent="flex-start"
                        alignItems="center"
                        px={2}
                        mb={2}
                    >
                        <Typography variant="h6" noWrap py={2}>
                            {`${user_id.firstname} ${user_id.lastname}`}
                        </Typography>
                    </Box>
                </Box>

                <Box
                    display="flex"
                    flexDirection="column"
                    flex={1}
                    px={2}
                    mb={2}
                >
                    <StyledTypography align="left" noWrap>
                        <b>{`Project: `}</b>
                        {project_id.name}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Log type: `}</b>
                        {getLogType(log_type_id)}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Week: `}</b>
                        {moment(date, FULL_DATE_FORMAT).isoWeek()}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Day: `}</b>
                        {moment(date, FULL_DATE_FORMAT).format(DATE_FORMAT)}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`Time spent: `}</b>
                        {formatTimeDuration(total)}
                    </StyledTypography>
                    <StyledTypography align="left" noWrap>
                        <b>{`From `}</b>
                        {from_time}
                        <b>{` to `}</b>
                        {to_time}
                    </StyledTypography>
                </Box>

                <Box
                    flex={4}
                    px={2}
                    display="flex"
                    flexDirection="column"
                    justifyContent={
                        status === "declined" ? "flex-start" : "space-between"
                    }
                    minWidth="200px"
                >
                    <Typography align="left" variant="h6" noWrap>
                        Descriptions
                    </Typography>
                    <StyledTextArea
                        id="descriptions"
                        onChange={function (e) {
                            formik.setFieldValue(
                                "descriptions",
                                e.currentTarget.value
                            );
                        }}
                        defaultValue={formik.values.descriptions}
                        error={!!formik.errors.descriptions}
                    />
                    <Box
                        pt={3}
                        display="flex"
                        flexDirection="row"
                        justifyContent="flex-end"
                        width="100%"
                        flexWrap="wrap"
                    >
                        {status !== "declined" && (
                            <>
                                <Box>
                                    <LogStatusHandler
                                        id={id}
                                        onUpdateLogStatus={onUpdateStatus}
                                    />
                                </Box>
                            </>
                        )}
                    </Box>
                </Box>
            </Box>
        </>
    );
}

export default InReviewItems;
