// Core
import React, { useEffect, useState } from "react";
import { useStores } from "store";
import { useObserver } from "mobx-react";
import moment from "moment";
import InfiniteScroll from "react-infinite-scroll-component";
// UI
import { Container, Box, Typography } from "@material-ui/core";
import { FULL_DATE_FORMAT } from "constants/dateFormats";
import ActivitiesList from "./ActivitiesList";

function useStore() {
    const { activityStore } = useStores();

    return useObserver(() => ({
        activities: activityStore.activities,
        onGetActivities: activityStore.onGetActivities,
        nextDate: activityStore.nextDate,
        setInitialState: activityStore.setInitialState,
    }));
}

export default function ReportHistory() {
    const [currentDate] = useState(moment().format(FULL_DATE_FORMAT));
    const {
        onGetActivities,
        activities,
        nextDate,
        setInitialState,
    } = useStore();

    useEffect(() => {
        onGetActivities({ date: currentDate });
        return () => {
            setInitialState();
        };
    }, [onGetActivities, currentDate, setInitialState]);

    return (
        <Container>
            <Box my={2}>
                <Typography variant="h6">Activity</Typography>
            </Box>
            <Box my={3} width="100%" maxWidth="1024px" mx="auto">
                <InfiniteScroll
                    dataLength={activities.length} //This is important field to render the next data
                    next={() => onGetActivities({ date: nextDate })}
                    hasMore={!!nextDate}
                    loader={<p>Loading...</p>}
                >
                    {activities.map((day, i) => (
                        <ActivitiesList
                            key={`date-${day.date}-${i}`}
                            activities={day.activities}
                            date={day.date}
                        />
                    ))}
                </InfiniteScroll>
            </Box>
        </Container>
    );
}
