// Core
import React, { useState, useEffect } from "react";
import { useObserver } from "mobx-react";
// UI
import { Grid, Container, Typography, Box } from "@material-ui/core";
import DevelopersTable from "./DevelopersTable";
import CreateUserForm from "./CreateUserForm";
import AddButton from "components/AddButton";
import ReportPagination from "components/ReportPagination";
import StyledPaper from "components/StyledPaper";

// Utils
import { useStores } from "../../store";

//Styles
import { StyledGrid } from "./styles";

function useStore() {
    const { userStore, paginationStore } = useStores();

    return useObserver(() => ({
        userList: userStore.userList,
        onGetUserList: userStore.onGetUserList,
        setEditingUserData: userStore.setEditingUserData,
        editingUserData: userStore.editingUserData,
        onCreateUser: userStore.onCreateUser,
        onUpdateUser: userStore.onUpdateUser,
        onDeleteUser: userStore.onDeleteUser,
        page: paginationStore.page,
        perPage: paginationStore.perPage,
    }));
}

export default function Projects() {
    const [isModalVisible, setModalVisibility] = useState(false);

    const {
        userList,
        onGetUserList,
        editingUserData,
        setEditingUserData,
        onCreateUser,
        onUpdateUser,
        onDeleteUser,
        page,
        perPage,
    } = useStore();

    const showModal = () => {
        setModalVisibility(true);
    };

    const closeModal = () => {
        setModalVisibility(false);
        setEditingUserData();
        onGetUserList({
            page: page,
            per_page: perPage,
        });
    };

    const handleDeleteUser = async id => {
        await onDeleteUser(id);
        closeModal();
    };

    useEffect(() => {
        onGetUserList({
            page: page,
            per_page: perPage,
        });
    }, [onGetUserList, page, perPage]);

    useEffect(() => {
        if (editingUserData) {
            showModal();
        }
    }, [editingUserData]);

    const onSubmitUser = async (data, id) => {
        editingUserData
            ? await onUpdateUser(data, id)
            : await onCreateUser(data);
        onGetUserList({
            page: page,
            per_page: perPage,
        });
        closeModal();
    };

    return (
        <Container>
            <Grid container>
                <StyledGrid item xs={12}>
                    <Typography variant="h6">Developers:</Typography>
                </StyledGrid>
                <Grid item xs>
                    <AddButton handleClick={showModal}>Add developer</AddButton>
                </Grid>
                <Grid item xs={12}>
                    <Box pb={5}>
                        <StyledPaper>
                            <DevelopersTable
                                developerList={userList}
                                setEditingUserData={setEditingUserData}
                            />
                            <ReportPagination />
                        </StyledPaper>
                    </Box>
                </Grid>
            </Grid>
            {userList.length && (
                <CreateUserForm
                    open={isModalVisible}
                    onClose={closeModal}
                    handleSubmit={onSubmitUser}
                    initialData={editingUserData}
                    handleDeleteUser={handleDeleteUser}
                />
            )}
        </Container>
    );
}
