import React from "react";
import moment from "moment";

import { Box, Typography, CardContent } from "@material-ui/core";
import { TIME_FORMAT, FULL_DATE_FORMAT } from "constants/dateFormats";
import { formatFromMinutes } from "utils/developersUtils";

const messages = {
    created: " created report on the project ",
    updated: " updated report on the project ",
    deleted: " deleted report on the project ",
};

function LogCard({ item }) {
    return (
        <Box component={CardContent} display="flex" flexDirection="column">
            <Typography variant="body1" gutterBottom>
                <b>{`${item.author.firstname} ${item.author.lastname} `}</b>
                {messages[item.event]}
                <b>{item.entity.project.name}</b>
            </Typography>
            <Typography variant="caption" color="textSecondary" gutterBottom>
                {` ${item.entity.descriptions}`}
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <b>{`From ${item.entity.from_time} to ${item.entity.to_time}; `}</b>
                <b>{`Total time: ${formatFromMinutes(item.entity.total)} `}</b>
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <i>Status: {item.entity.status}</i>
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <i>Log type: {item.entity.log_type_id}</i>
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <i>
                    Created at:{" "}
                    {moment(item.entity.created_at).format(
                        `${FULL_DATE_FORMAT} ${TIME_FORMAT}`
                    )}
                </i>
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <i>
                    Updated at:{" "}
                    {moment(item.entity.updated_at).format(
                        `${FULL_DATE_FORMAT} ${TIME_FORMAT}`
                    )}
                </i>
            </Typography>
        </Box>
    );
}

export default LogCard;
