// Core
import React from "react";
import { useFormik } from "formik";
import { object, string } from "yup";
// UI
import { Grid, TextField, Box } from "@material-ui/core";
import DialogActions from "@material-ui/core/DialogActions";
import FormDialog from "components/FormDialog";
import SkillMultiSelect from "../SkillsSelector";
import DialogActionButton from "components/DialogActionButton";

// Styles
import { StyledGridContainer } from "./styles";

// Utils
import { getInitialFormValues } from "utils/developersUtils";
import RoleSelector from "../RoleSelector";

let schema = object().shape({
    firstname: string().required(),
    lastname: string().required(),
    email: string().email().required(),
    role: string().required(),
});

export default function CreateUserForm({
    open,
    onClose,
    handleSubmit,
    initialData,
    handleDeleteUser,
}) {
    const initialValues = getInitialFormValues(initialData);

    const formik = useFormik({
        initialValues,
        enableReinitialize: true,
        validateOnChange: false,
        validateOnBlur: false,
        validationSchema: schema,
        onSubmit: (data, { resetForm }) => {
            handleSubmit(data, initialData?.id).then(() =>
                resetForm(getInitialFormValues())
            );
        },
    });

    const onCancel = () => {
        formik.resetForm(getInitialFormValues());
        onClose();
    };

    const { errors } = formik;

    return (
        <FormDialog open={open} handleClose={onClose} title="Developer:">
            <form autoComplete="off">
                <StyledGridContainer>
                    <Grid container>
                        <Box mr={1}>
                            <TextField
                                required
                                error={!!errors.firstname}
                                id="firstname"
                                label="First name"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.firstname}
                                autoComplete="off"
                            />
                        </Box>
                        <Box ml={1}>
                            <TextField
                                required
                                error={!!errors.lastname}
                                id="lastname"
                                label="Last name"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.lastname}
                                autoComplete="off"
                            />
                        </Box>
                    </Grid>
                    <Grid container justify={"space-between"}>
                        <Grid item xs={4}>
                            <TextField
                                id="country"
                                label="Country"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.country}
                            />
                        </Grid>
                        <Grid item xs={7}>
                            <TextField
                                required
                                error={!!errors.email}
                                fullWidth
                                id="email"
                                label="e-mail"
                                onChange={formik.handleChange}
                                defaultValue={formik.values.email}
                            />
                        </Grid>
                    </Grid>
                    <Grid container>
                        <RoleSelector
                            value={formik.values.role}
                            error={!!errors.role}
                            onChange={formik.setFieldValue}
                            id="role"
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <SkillMultiSelect
                            values={formik.values.skills}
                            onChange={formik.setFieldValue}
                            id="skills"
                        />
                    </Grid>
                    <DialogActions>
                        {initialData && (
                            <DialogActionButton
                                onClick={() =>
                                    handleDeleteUser(initialData?.id)
                                }
                                type="error"
                            >
                                Delete
                            </DialogActionButton>
                        )}
                        <DialogActionButton onClick={onCancel} type="default">
                            Cancel
                        </DialogActionButton>
                        <DialogActionButton
                            onClick={formik.handleSubmit}
                            variant="contained"
                            type="success"
                        >
                            Save
                        </DialogActionButton>
                    </DialogActions>
                </StyledGridContainer>
            </form>
        </FormDialog>
    );
}
