// Core
import React from "react";

// UI
import { TableHead, TableBody, Table } from "@material-ui/core";
import TableRow from "components/TableRow";
import TableCell from "components/TableCell";

// Constants
import { TABLE_HEADERS } from "constants/developers";
import { formatFromMinutes } from "utils/developersUtils";

export default function ({ developerList, setEditingUserData }) {
    const getFullName = (firstname, lastname) => `${firstname} ${lastname}`;
    const concatSkills = skillsArray => skillsArray.join(", ");

    return (
        <Table aria-label="simple table">
            <TableHead>
                <TableRow>
                    {TABLE_HEADERS.map(item => (
                        <TableCell key={item}>{item}</TableCell>
                    ))}
                </TableRow>
            </TableHead>
            <TableBody>
                {developerList.length ? (
                    developerList.map(developer => (
                        <TableRow
                            type={
                                developer.status === "inactive"
                                    ? "disabled"
                                    : "primary"
                            }
                            hover
                            key={developer.id}
                            onClick={() => setEditingUserData(developer)}
                        >
                            <TableCell component="th" scope="row">
                                {developer.id}
                            </TableCell>
                            <TableCell>
                                {getFullName(
                                    developer.firstname,
                                    developer.lastname
                                )}
                            </TableCell>
                            <TableCell>
                                {concatSkills(developer.skills)}
                            </TableCell>
                            <TableCell>{developer.projects}</TableCell>
                            <TableCell>
                                {formatFromMinutes(developer.total_work_time)}
                            </TableCell>
                            <TableCell>{developer.status}</TableCell>
                        </TableRow>
                    ))
                ) : (
                    <TableRow>
                        <TableCell>{"Empty"}</TableCell>
                    </TableRow>
                )}
            </TableBody>
        </Table>
    );
}
