// Core
import React, { useEffect, useState, useCallback } from "react";
import { useObserver } from "mobx-react";
// UI
import FilterSelectorPanel from "./FilterSelectorPanel";
import { Container, Box } from "@material-ui/core";
import LogTableReports from "./LogReportTable";
import ReportPagination from "components/ReportPagination";
import DownloadButton from "components/DownloadButton";

// Store
import { useStores } from "store";

// Utils
import { getWeekNumber, formatToString } from "utils/managerReportsUtils";

// Styles
import { StyledPaper } from "./style";

function useStore() {
    const { logStore, exportStore, paginationStore } = useStores();

    return useObserver(() => ({
        logs: logStore.logs,
        setEmptyLogs: logStore.setEmptyLogs,
        projectList: logStore.projectList,
        userList: logStore.userList,
        onGetLogs: logStore.onGetLogs,
        onGetProjectList: logStore.onGetProjectList,
        onGetUserList: logStore.onGetUserList,
        onUpdateLogStatus: logStore.onUpdateLogStatus,
        onDownload: exportStore.onDownload,
        page: paginationStore.page,
        perPage: paginationStore.perPage,
        setInitialPagination: paginationStore.setInitialValues,
    }));
}

export default function ManagerReports() {
    const {
        logs,
        projectList,
        onGetLogs,
        userList,
        page,
        perPage,
        onDownload,
        onGetUserList,
        onGetProjectList,
        setInitialPagination,
        setEmptyLogs,
    } = useStore();

    const currentWeek = getWeekNumber(new Date());

    const [selectedWeeks, setSelectedWeeks] = useState([currentWeek]);
    const [fromDate, setFromDate] = useState(null);
    const [toDate, setToDate] = useState(null);
    const [selectedProjects, setSelectedProjects] = useState([]);
    const [selectedUsers, setSelectedUsers] = useState([]);

    const [errorFromDate, setErrorFromDate] = useState(false);
    const [errorToDate, setErrorToDate] = useState(false);

    const onErrorDates = (type, value) => {
        const setValue = {
            dateFrom: () => setErrorFromDate(value),
            dateTo: () => setErrorToDate(value),
        };

        setValue[type]();
    };
    useEffect(() => {
        // TODO: add year logic
        const addYearToWeek = selectedWeeks.map(week => `${week},2020`);

        onGetLogs({
            users: selectedUsers.map(item => item.id),
            projects: selectedProjects.map(item => item.id),
            weeks: addYearToWeek,
            page,
            per_page: perPage,
        });
    }, [
        onGetLogs,
        page,
        perPage,
        selectedProjects,
        selectedUsers,
        selectedWeeks,
        setEmptyLogs,
    ]);

    useEffect(() => {
        if (selectedWeeks.length) {
            setFromDate(null);
            setToDate(null);
        }
    }, [selectedWeeks]);

    useEffect(() => {
        if (fromDate && toDate) {
            if (!errorFromDate && !errorToDate) {
                onGetLogs({
                    users: selectedUsers.map(item => item.id),
                    from_date: formatToString(fromDate),
                    to_date: formatToString(toDate),
                    projects: selectedProjects.map(item => item.id),
                    page,
                    per_page: perPage,
                });
            }
        }
    }, [
        fromDate,
        toDate,
        errorFromDate,
        errorToDate,
        onGetLogs,
        selectedUsers,
        selectedProjects,
        page,
        perPage,
    ]);

    const onFilterChange = useCallback(
        (type, value) => {
            setInitialPagination();
            const setValueTypes = {
                fromDate: () => {
                    setFromDate(value);
                    setSelectedWeeks([]);
                },
                toDate: () => {
                    setToDate(value);
                    setSelectedWeeks([]);
                },
                selectedWeeks: () => setSelectedWeeks(value),
                selectedProjects: () => setSelectedProjects(value),
                selectedUsers: () => setSelectedUsers(value),
            };

            setValueTypes[type]();
        },
        [setInitialPagination]
    );

    useEffect(() => {
        onGetProjectList();
        onGetUserList();

        return setEmptyLogs;
    }, [onGetProjectList, onGetUserList, setEmptyLogs]);

    const onDownloadFile = () => {
        onDownload({
            from_date: fromDate ? formatToString(fromDate) : null,
            to_date: toDate ? formatToString(toDate) : null,
            week: selectedWeeks,
            users: selectedUsers.map(item => item.id),
            projects: selectedProjects.map(item => item.id),
        });
    };

    return (
        <Container>
            <FilterSelectorPanel
                fromDate={fromDate}
                toDate={toDate}
                selectedWeeks={selectedWeeks}
                selectedProjects={selectedProjects}
                selectedUsers={selectedUsers}
                setFilter={onFilterChange}
                projectList={projectList}
                logs={logs}
                setErrorDates={onErrorDates}
                userList={userList}
            />
            <Box mt={4} mb={5}>
                <Box display="flex" my={3}>
                    {
                        <DownloadButton
                            disabled={logs.length === 0}
                            handleClick={onDownloadFile}
                        >
                            Export
                        </DownloadButton>
                    }
                </Box>
                <StyledPaper>
                    <LogTableReports logList={logs} />
                    {logs?.length > 0 && <ReportPagination />}
                </StyledPaper>
            </Box>
        </Container>
    );
}
