// Core
import React from "react";
// UI
import { Autocomplete } from "@material-ui/lab";
import { TextField } from "@material-ui/core";

// Styles
import { getLastWeek } from "utils/managerReportsUtils";

const ALL_WEEKS = Array.from({ length: getLastWeek() }).map((empty, index) =>
    String(index + 1)
);

function WeekSelector({ setFilter, selectedWeeks }) {
    const onChangeWeeks = (e, value) => {
        setFilter("selectedWeeks", value);
    };

    const getOptionSelected = (option, value) =>
        Number(option) === Number(value);

    const getOptionLabel = option => option;

    return (
        <Autocomplete
            multiple
            value={selectedWeeks}
            options={ALL_WEEKS}
            onChange={onChangeWeeks}
            filterSelectedOptions
            getOptionSelected={getOptionSelected}
            getOptionLabel={getOptionLabel}
            renderInput={params => (
                <TextField {...params} label="Week number" />
            )}
        />
    );
}

export default WeekSelector;
