import moment from "moment";
import {
    MONDAY,
    SUNDAY,
    FULL_DATE_FORMAT,
    HOUR_IN_MINUTES,
} from "constants/dateFormats";

export const getWeekNumber = date => moment(date, FULL_DATE_FORMAT).isoWeek();

export const getFromDate = weekNumber =>
    moment().isoWeek(weekNumber).isoWeekday(MONDAY).format(FULL_DATE_FORMAT);

export const getToDate = weekNumber => {
    const date = moment().isoWeek(weekNumber).isoWeekday(SUNDAY);
    return date.format(FULL_DATE_FORMAT);
};

export const formatToString = momentDate =>
    moment(momentDate).format(FULL_DATE_FORMAT);

export const getMomentInstance = date =>
    date ? moment(date, FULL_DATE_FORMAT) : null;

export const checkIfWeekSelected = (fromDate, toDate) => {
    const fromDateWeekNumber = getWeekNumber(fromDate);
    const toDateWeekNumber = getWeekNumber(toDate);
    if (fromDateWeekNumber === toDateWeekNumber) {
        const startOfWeek = getMomentInstance(fromDate).startOf("isoWeek");
        const endOfWeek = getMomentInstance(fromDate).endOf("isoWeek");
        return (
            getMomentInstance(fromDate).isSame(startOfWeek, "day") &&
            getMomentInstance(toDate).isSame(endOfWeek, "day")
        );
    }
    return false;
};

export const getLogsParams = (fromDate, toDate, users, projects, weeks) => {
    return {
        from_date: fromDate,
        to_date: toDate,
        users: users.map(item => item.id),
        projects: projects.map(item => item.id),
        weeks,
    };
};

export const formatTimeDuration = time => {
    const durationInMinutes = moment.duration(time).asMinutes();
    if (durationInMinutes < HOUR_IN_MINUTES) {
        return `${durationInMinutes} min`;
    }
    return `${moment.duration(time).hours()} h ${
        moment.duration(time).minutes()
            ? moment.duration(time).minutes() + " m"
            : ""
    }`;
};

export const getLastWeek = () => moment().endOf("year").isoWeeks();
