// Core
import React from "react";
// UI
import { Autocomplete } from "@material-ui/lab";
import Chip from "@material-ui/core/Chip";

// Components
import ColoredName from "components/ColoredName";

// Styles
import { StyledTextField } from "./style";

function ProjectSelector({ projects, setFilter, selectedProjects }) {
    const onChangeProject = (e, value) => {
        setFilter("selectedProjects", value);
    };

    const getOptionSelected = (option, value) => option.id === value?.id;

    const getOptionLabel = option => option.name;

    return (
        <Autocomplete
            multiple
            renderTags={listProjects =>
                listProjects.map(project => (
                    <Chip
                        key={project.id}
                        label={
                            <ColoredName
                                text={project.name}
                                color={project.color}
                                fontSize={15}
                            />
                        }
                        onDelete={e => {
                            const filteredProjects = listProjects.filter(
                                ({ id }) => {
                                    return id !== project.id;
                                }
                            );

                            return onChangeProject(e, filteredProjects);
                        }}
                    />
                ))
            }
            value={selectedProjects}
            options={projects}
            onChange={onChangeProject}
            filterSelectedOptions
            getOptionSelected={getOptionSelected}
            getOptionLabel={getOptionLabel}
            renderInput={params => (
                <StyledTextField {...params} label="Project name" />
            )}
        />
    );
}

export default ProjectSelector;
