// Core
import React from "react";
import moment from "moment";

// UI
import {
    TableHead,
    TableBody,
    Table,
    Box,
    Typography,
} from "@material-ui/core";
import TableRow from "components/TableRow";
import TableCell from "components/TableCell";
import Chip from "components/Chip";
import StyledPaper from "components/StyledPaper";
import ColoredName from "components/ColoredName";

// Constants
import { TABLE_HEADERS } from "constants/developerReport";
import { FULL_DATE_SHORT, FULL_DATE_FORMAT } from "constants/dateFormats";

// Utils
import { getTotalTime, formatTimeDuration } from "utils/developerReportUtils";

// Styles
import { StyledTableCell, StyledTableContainer } from "./style";

export default function ({
    logList,
    handleEditLog,
    selectedDay,
    selectedWeek,
    projects,
    logTypes,
    setTotalWeekTime,
    setTotalDayTime,
}) {
    const [filteredLogs, setFilteredLogs] = React.useState([]);

    React.useEffect(() => {
        let logs = logList;
        if (selectedDay) {
            logs = logList.filter(item =>
                moment(item.date, FULL_DATE_FORMAT).isSame(
                    moment(selectedDay),
                    "day"
                )
            );
        }
        if (!selectedWeek) {
            logs = [];
        }
        setFilteredLogs(logs);
    }, [logList, selectedDay, selectedWeek]);

    React.useEffect(() => {
        setTotalWeekTime(getTotalTime(logList));
    }, [logList, setTotalWeekTime]);

    React.useEffect(() => {
        setTotalDayTime(getTotalTime(filteredLogs));
    }, [filteredLogs, setTotalDayTime]);

    const editLog = log => {
        const validLog = { ...log, project_id: log.project_id?.id };

        handleEditLog(validLog);
    };

    const getLogTypeEnum = id => logTypes.find(item => item.id === id)?.type;
    const getType = status =>
        status === "approved"
            ? "success"
            : status === "declined"
            ? "error"
            : "default";

    return (
        <StyledTableContainer component={StyledPaper}>
            <Table>
                <TableHead>
                    <TableRow>
                        {TABLE_HEADERS.map(item => (
                            <TableCell key={item}>{item}</TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {filteredLogs.length
                        ? filteredLogs.map((log, i) => (
                              <TableRow
                                  hover
                                  key={log.id}
                                  onClick={() => editLog(log)}
                              >
                                  <TableCell>{i + 1}</TableCell>
                                  <TableCell>
                                      {moment(
                                          log.date,
                                          FULL_DATE_FORMAT
                                      ).format(FULL_DATE_SHORT)}
                                  </TableCell>
                                  <TableCell>
                                      <ColoredName
                                          text={log.project_id?.name}
                                          color={log.project_id?.color}
                                          fontSize={16}
                                      />{" "}
                                  </TableCell>
                                  <TableCell>
                                      {getLogTypeEnum(log.log_type_id)}
                                  </TableCell>
                                  <TableCell>{log.from_time}</TableCell>
                                  <TableCell>{log.to_time}</TableCell>
                                  <TableCell>
                                      {formatTimeDuration(log.total)}
                                  </TableCell>
                                  <StyledTableCell>
                                      <Chip
                                          type={getType(log.status)}
                                          label={log.status}
                                      />
                                  </StyledTableCell>
                              </TableRow>
                          ))
                        : null}
                </TableBody>
            </Table>
            {!filteredLogs.length && (
                <Typography variant="subtitle1">
                    <Box p={2}>{"You don't have any logs"}</Box>
                </Typography>
            )}
        </StyledTableContainer>
    );
}
