// Core
import React from "react";
import moment from "moment";

// UI
import { TableHead, TableBody, Table, Box } from "@material-ui/core";
import TableRow from "components/TableRow";
import TableCell from "components/TableCell";
import ColoredName from "components/ColoredName";

// Constants
import { TABLE_HEADERS } from "constants/managerReports";

// Utils
import { formatTimeDuration } from "utils/managerReportsUtils";

// Styles
import { ColoredStatus } from "../style";

export default function LogReportTable({ logList }) {
    return (
        <Table aria-label="simple table">
            <TableHead>
                <TableRow>
                    {TABLE_HEADERS.map(item => (
                        <TableCell noWrap key={item}>
                            {item}
                        </TableCell>
                    ))}
                </TableRow>
            </TableHead>
            <TableBody>
                {logList.length ? (
                    logList.map(log => (
                        <TableRow hover key={`log-${log.id}`}>
                            <TableCell noWrap>
                                {moment(log.date, "DD-MM-YYYY").format(
                                    "DD.MM.YYYY"
                                )}
                            </TableCell>
                            <TableCell align="center">
                                {moment(log.date, "DD-MM-YYYY").isoWeek()}
                            </TableCell>
                            <TableCell>{`${log.user_id.firstname} ${log.user_id.lastname}`}</TableCell>
                            <TableCell>
                                <ColoredName
                                    text={log.project_id.name}
                                    fontSize={14}
                                    color={log.project_id.color}
                                />
                            </TableCell>
                            <TableCell>{log.descriptions}</TableCell>
                            <TableCell noWrap>
                                {formatTimeDuration(log.total)}
                            </TableCell>
                            <TableCell>
                                {log.last_generated
                                    ? moment(
                                          log.last_generated,
                                          "YYYY-MM-DD HH:mm:ss"
                                      ).calendar()
                                    : "-"}
                            </TableCell>
                            <TableCell>
                                <ColoredStatus status={log.status}>
                                    {log.status}
                                </ColoredStatus>
                            </TableCell>
                        </TableRow>
                    ))
                ) : (
                    <TableRow>
                        <Box p={3} component="td">
                            {"Empty"}
                        </Box>
                    </TableRow>
                )}
            </TableBody>
        </Table>
    );
}
