// Core
import React from "react";
import moment from "moment";

// UI
import { Typography, CardContent, Box } from "@material-ui/core";
import { TIME_FORMAT, FULL_DATE_FORMAT } from "constants/dateFormats";

// Styles
import { StyledProjectName } from "./styles";

const messages = {
    created: "created project",
    updated: "updated project",
    deleted: "deleted project",
    added_user_to_project: " added user to project",
    deleted_user_to_project: " deleted user from project",
};

function ProjectCard({ item }) {
    return (
        <Box component={CardContent} display="flex" flexDirection="column">
            <Typography variant="body1" gutterBottom>
                <b>{`${item.author.firstname} ${item.author.lastname} `}</b>
                {messages[item.event]}
                <StyledProjectName
                    color={item.entity.color}
                >{` ${item.entity.name}`}</StyledProjectName>
            </Typography>
            <Typography variant="caption" color="textSecondary" gutterBottom>
                {item.entity.descriptions}
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <i>Type: {item.entity.type}</i>
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <i>
                    Created at:{" "}
                    {moment(item.entity.created_at).format(
                        `${FULL_DATE_FORMAT} ${TIME_FORMAT}`
                    )}
                </i>
            </Typography>
            <Typography variant="caption" color="textSecondary">
                <i>
                    Updated at:{" "}
                    {moment(item.entity.updated_at).format(
                        `${FULL_DATE_FORMAT} ${TIME_FORMAT}`
                    )}
                </i>
            </Typography>
        </Box>
    );
}

export default ProjectCard;
