// Core
import React, { useEffect, useState, useCallback } from "react";
import { useStores } from "store";
import { useObserver } from "mobx-react";
// UI
import { Container, Box, Paper, Tabs, Tab } from "@material-ui/core";
import InReviewItem from "./InReviewItem";
import ReportPagination from "components/ReportPagination";

const STATUS_VALUES = ["in review", "declined"];

function useStore() {
    const { logStore, paginationStore } = useStores();

    return useObserver(() => ({
        logs: logStore.logs,
        logTypes: logStore.logTypes,
        onGetLogs: logStore.onGetLogs,
        onUpdateLog: logStore.onUpdateLog,
        setEmptyLogs: logStore.setEmptyLogs,
        onUpdateLogStatus: logStore.onUpdateLogStatus,
        onGetLogTypes: logStore.onGetLogTypes,
        page: paginationStore.page,
        perPage: paginationStore.perPage,
    }));
}

export default function LogItems() {
    const {
        logs,
        onGetLogs,
        page,
        perPage,
        onGetLogTypes,
        logTypes,
        onUpdateLog,
        onUpdateLogStatus,
        setEmptyLogs,
    } = useStore();

    const [selectedStatus, setValue] = useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const updateLogs = useCallback(() => {
        onGetLogs({
            page,
            status: [STATUS_VALUES[selectedStatus]],
            per_page: perPage,
        });
    }, [onGetLogs, page, perPage, selectedStatus]);

    useEffect(() => {
        onGetLogTypes();
    }, [onGetLogTypes]);

    useEffect(() => {
        updateLogs();
    }, [updateLogs]);

    useEffect(() => {
        return setEmptyLogs;
    }, [setEmptyLogs]);

    const updateLogStatus = async data => {
        await onUpdateLogStatus(data);
        updateLogs();
    };

    return (
        <>
            <Paper>
                <Tabs
                    value={selectedStatus}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    centered
                >
                    <Tab label="In review" />
                    <Tab label="Declined" />
                </Tabs>
            </Paper>
            <Container>
                <Box my={2} margin="auto" width="90%" maxWidth="900px">
                    {logs.map(item => (
                        <InReviewItem
                            {...item}
                            key={`log-item-${item.id}`}
                            onUpdateLog={onUpdateLog}
                            onUpdateLogStatus={updateLogStatus}
                            logTypes={logTypes}
                        />
                    ))}
                    {logs?.length > 0 && <ReportPagination />}
                    {!logs?.length && (
                        <Box my={2} textAlign="center">
                            You don`t have any log items for review
                        </Box>
                    )}
                </Box>
            </Container>
        </>
    );
}
